<template>
  <a-card
    style="width: 100%; border-radius: 10px"
    :tab-list="tabList"
    :active-tab-key="tab"
    @tabChange="(key) => onTabChange(key)"
  >
    <SalesData ref="refSalesData" v-if="tab === 'sales'" />
    <VisitsData ref="refSalesData" v-if="tab === 'visits'" />

    <div slot="tabBarExtraContent">
      <a
        v-for="item in dateList"
        :key="item.key"
        class="mr20"
        href="#"
        @click="() => handleDateClick(item.key)"
      >
        {{ item.value }}
      </a>
      <a-range-picker v-model="dateRange" :format="dateFormat" />
    </div>
  </a-card>
</template>

<script>
import SalesData from "./SalesData.vue";
import VisitsData from "./VisitsData.vue";
import moment from "moment";

export default {
  name: "SalesDataCard",
  components: {
    SalesData,
    VisitsData,
  },
  data() {
    return {
      tabList: [
        {
          key: "sales",
          tab: "",
        },
        {
          key: "visits",
          tab: "",
        },
      ],
      tab: "sales",
      dateList: [
        {
          key: "6",
          value: "6",
        },
        {
          key: "3",
          value: "3",
        },
        {
          key: "30",
          value: "30",
        },
        {
          key: "7",
          value: "7",
        },
      ],
      dateRange: [],
      dateFormat: "YYYY-MM-DD",
    };
  },
  mounted() {
    this.onTabChange(this.tab);
  },
  methods: {
    onTabChange(key) {
      this.tab = key;

      setTimeout(() => {
        this.$refs.refSalesData.renderChart();
      });
    },
    onDateChange() {},
    handleDateClick(key) {
      this.dateRange[1] = moment().format(this.dateFormat);

      switch (key) {
        case "6":
          this.dateRange[0] = moment()
            .subtract(6, "months")
            .format(this.dateFormat);
          break;
        case "3":
          this.dateRange[0] = moment()
            .subtract(3, "months")
            .format(this.dateFormat);
          break;
        case "30":
          this.dateRange[0] = moment()
            .subtract(30, "days")
            .format(this.dateFormat);
          break;
        case "7":
          this.dateRange[0] = moment()
            .subtract(7, "days")
            .format(this.dateFormat);
          break;
        default:
          break;
      }

      this.dateRange = [...this.dateRange];
    },
  },
};
</script>

<style lang="less" scoped>
.mr20 {
  margin-right: 20px;
}
</style>
